<template>
    <div>
        <!--岗位类别-->
        <div v-for="(item,index) in jobList" :key="index">
            <el-row class="row">
                <el-col :span="8">
                    <div class="grid-content bg-purple label">岗位类别：</div>
                </el-col>
                <el-col :span="6">
                    <div class="grid-content bg-purple-light input">
                        <el-cascader class="input-major"
                                     :options="jobCategory"
                                     @change="handleChange"></el-cascader>
                    </div>
                </el-col>
                <el-col :span="5" class="input">
                    <el-input class="input-inner-number"
                              size="medium"
                              placeholder="请输入内容"
                              v-model="jobList[index]['count']"
                              type="number"
                              @change="handleChange2(index)"
                              clearable>
                    </el-input>
                </el-col>
                <el-col :span="5" class="input" v-if="index>0">
                    <el-button type="danger" round @click="delHandle(index)">删除</el-button>
                </el-col>
            </el-row>
        </div>
        <el-button type="info" @click="addHandle">添加</el-button>
    </div>
</template>

<style lang="scss" scoped>
    .input {
        text-align: left;
        height: 40px;
        line-height: 40px;
    }
</style>

<script>
    import axios from 'axios'
    import url from '@/service.config.js'

    export default {
        data() {
            return {
                jobList: [{'学历结构': []}],
                job: [],
                index: null,
                jobCategory: [],
            }
        },
        methods: {
            handleChange(value) {
                this.$data.job = value;
                console.log(value)
                this.$emit('input', this.$data.jobList)
            },
            handleChange2(index) {
                console.log(index)
                this.$data.index = index
                this.$data.jobList[index]['学历结构'] = this.$data.job
                this.$emit('input', this.$data.jobList)
            },
            delHandle(index){
                this.$data.jobList.splice(index, 1);
            },
            addHandle(){
                this.$data.jobList.push({'学历结构': []});
            }
        },
        created() {
            axios({
                url: url.getJobCategory,
                methods: 'get',
                params: {}
            }).then(res => {
                console.log(res)
                this.$data.jobCategory = res.data.jobCategory
            }).catch(err => {
                console.log(err)
            });
        }
    }
</script>