<template>
    <div>
        <!--岗位类别-->
        <div v-for="(item,index) in cdata" :key="index">
            <div class="box marin">
                <div class="box-item1">
                    岗位类别：
                </div>
                <div class="box-item2">
                    <el-cascader class="input-major"
                                 :options="list"
                                 @change="changeHandle"
                                 v-model="item['岗位类别']"
                    >
                    </el-cascader>
                </div>
                <div class="box-item3">
                    <el-input class="input-inner-number"
                              size="medium"
                              placeholder="请输入内容"
                              @change="changeHandle"
                              v-model="item['count']"
                              type="number"
                              clearable>
                    </el-input>
                </div>
                <div class="box-item4">
                    <el-button v-if="index>0" type="danger" round @click="delHandle(index)">删除</el-button>
                </div>
            </div>
        </div>
        <div class="marin">
            <el-button type="info" @click="addHandle">添加</el-button>
        </div>
    </div>
</template>

<style lang="scss" scoped>
    .marin {
        margin-bottom: 10px;
    }
    .box {
        display: flex;
        &-item1 {
            width: 100px;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        &-item2 {
            width: 300px;
        }
        &-item3 {
            width: 200px;
            height: 40px;
            line-height: 40px;
            margin-left: 10px;
        }
        &-item4 {
            width: 100px;
            height: 40px;
            line-height: 40px;
            margin-left: 10px;
        }
    }
</style>

<script>
    import axios from 'axios';
    import url from '@/service.config.js';

    export default {
        data() {
            return {
                list: [],
                cdata: [{}],
            }
        },
        methods: {
            changeHandle(){
                this.$emit('input',this.$data.cdata)
            },
            addHandle(){
                this.$data.cdata.push({});
            },
            delHandle(index){
                this.$data.cdata.splice(index, 1); //移除对应索引下的对象
            },
        },
        created() {
            axios({
                url: url.getJobCategory,
                methods: 'get',
                params: {}
            }).then(res => {
                this.$data.list = res.data.jobCategory;
            }).catch(err => {
            })
        },
    }
</script>