<template>
    <div>
        <LossTalentInfo v-model="result"></LossTalentInfo>
        <el-button type="primary" @click="dialogVisible = true">提交</el-button>

        <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                width="30%"
                :before-close="handleClose">
            <span>当前问卷一旦提交无法修改，确认提交吗？</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
              </span>
        </el-dialog>
    </div>
</template>

<style lang="scss" scoped>

</style>

<script>
    import LossTalentInfo from '../components/LossTalentInfo.vue';
    import axios from 'axios'
    import url from '@/service.config.js'

    export default {
        data(){
            return {
                dialogVisible: false,
                result: [],
            }
        },
        methods: {
            handleClose(done) {
                this.$confirm('确认关闭？')
                    .then(_ => {
                        done();
                    })
                    .catch(_ => {
                    });
            },
            submit() {
                this.dialogVisible = false
                let obj = {'该单位人才流失人员信息统计':this.$data.result};

                let data= obj;
                axios({
                    url: url.talentLossInfo,
                    method: 'post',
                    data: data
                }).then(res=>{
                    if(res.data=='success'){
                        this.$message({
                            message: '填写成功，感谢您的填写！',
                            type: 'success'
                        });
                    }
                }).catch(err=>{
                    this.$message.error('后台服务未启动');

                })

            }
        },
        components: {
            LossTalentInfo,
        }
    }
</script>