<template>
    <div>
        <el-tabs tab-position="left" style="height: 1600px;">
            <el-tab-pane v-for="(item,index) in result" :key="index" :label="item['year']">
                <p class="title">单位人才情况汇总表-存量信息-{{item.year}}</p>
                <TalentStock2 v-model="result[index]['info']"></TalentStock2>
                <el-button v-if="index==9" type="primary" @click="dialogVisible = true">提交</el-button>
            </el-tab-pane>
        </el-tabs>
        <el-dialog
                title="提示"
                :visible.sync="dialogVisible"
                width="30%"
                :before-close="handleClose">
            <span>当前问卷一旦提交无法修改，确认提交吗？</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
              </span>
        </el-dialog>
    </div>
</template>

<style lang="scss" scoped>

</style>

<script>
    import TalentStock2 from '../components/TalentStock2.vue';
    import axios from 'axios'
    import url from '@/service.config.js'
    export default {
        data(){
            return {
                dialogVisible: false,
                result: [{'year':'2009'},{'year':'2010'},{'year':'2011'},{'year':'2012'},{'year':'2013'},{'year':'2014'},{'year':'2015'},{'year':'2016'},{'year':'2017'},{'year':'2018'}],
            }
        },
        methods: {
            handleClose(done) {
                this.$confirm('确认关闭？')
                    .then(_ => {
                        done();
                    })
                    .catch(_ => {
                    });
            },
            submit() {
                this.dialogVisible = false
                let obj = {'该单位10年的单位人才存量统计':this.$data.result};

                let data= obj;
                axios({
                    url: url.tenYearTalentCount,
                    method: 'post',
                    data: data
                }).then(res=>{
                    if(res.data=='success'){
                        this.$message({
                            message: '填写成功，感谢您的填写！',
                            type: 'success'
                        });
                    }
                }).catch(err=>{
                    this.$message.error('后台服务未启动');

                })

            }
        },
        components: {
            TalentStock2,
        },
    }
</script>