import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        company: '哦哦哦',
    },
    mutations: {
        companyChange(state, status) {
            state.company = status;
        }
    },
    actions: {
        companyChangeAction({commit}, name) {
            commit('companyChange', name)
        }
    }
})
