import Vue from 'vue'
import Router from 'vue-router'

import DemandInvestigation from './views/DemandInvestigation.vue';
import TalentStatus from './views/TalentStatus.vue';
import TalentFlow from './views/TalentFlow.vue';
import TalentFlowIn from './views/TalentFlowIn.vue';
import TalentFlowOut from './views/TalentFlowOut.vue';
import LossTalentInfo from './views/LossTalentInfo.vue';
import CompanyInfo from './views/CompanyInfo.vue';

import Test from './views/Test.vue';

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'companyInfo',
            component: CompanyInfo
        },
        {
            path: '/demandInvestigation',
            name: 'demandInvestigation',
            component: DemandInvestigation
        },
        {
            path: '/talentStatus',
            name: 'talentStatus',
            component: TalentStatus
        },
        {
            path: '/talentFlow',
            name: 'talentFlow',
            component: TalentFlow
        },
        {
            path: '/talentFlowIn',
            name: 'talentFlowIn',
            component: TalentFlowIn
        },
        {
            path: '/talentFlowOut',
            name: 'talentFlowOut',
            component: TalentFlowOut
        },
        {
            path: '/lossTalentInfo',
            name: 'lossTalentInfo',
            component: LossTalentInfo
        },

        {
            path: '/test',
            name: 'test',
            component: Test
        }
    ]
})
